.custom-select {
  width: auto;
}

/* Styling for dropdown groups and options */

/* Group label styling */
.form-select optgroup {
  font-weight: 600;
  font-size: 14px;
  /* color: #080808;
  background-color: #f8f9fa; */
  padding: 5px;
  border-bottom: 1px solid #dee2e6;
}

/* Option styling */
.form-select option {
  padding: 8px 12px;
  font-size: 14px;
}

/* EBOM Attributes group specific styling */
.form-select optgroup[label="EBOM Attributes"] {
  color: #080808; /* Bootstrap primary blue color */
  background-color: #7195ca87 /* Light blue background */
}

/* Part Attributes group specific styling */
.form-select optgroup[label="Item Attributes"] {
  color: #080808; /* Bootstrap success green color */
  background-color: rgba(197, 201, 201, 0.904); /* Light green background */
}

